html {
    font-size: 17px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

* {
    box-sizing: border-box;
    letter-spacing: .01rem;
}

h1,
h2,
h3,
h4 {
    padding: 0;
    margin: 0;
    color: var(--color-black);
    line-height: 1.5;
}

body {
    margin: 0;
    padding: 0;
    user-select: none;
    -webkit-user-select: none;
    overflow: hidden;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(241, 243, 244);
}

.flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

main {
    padding: 4rem;
    overflow: hidden;
    overflow-y: auto;
}

header h1,
h2 {
    color: rgb(187, 90, 21);
    width: 100%;
    text-align: center;
}

main h2 {
    font-size: 3em;
    color: #002;
}

button {
    outline: none;
    border: none;
    padding: .5rem 1rem;
    margin: .7rem;
    min-width: auto;
    cursor: pointer;
    border-radius: 7px;
    background-color: chocolate;
    color: #fff;
    font-weight: 400;
    font-size: .9em;
    border: 2px solid chocolate;
}

#reset {
    background-color: transparent;
    color: chocolate;
}

.disabled {
    opacity: .4;
    cursor: not-allowed;
}

section { margin: 1rem 0;}

section.btns-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap-reverse;
}

section.btn-auto-con {
    width: 100%;
    justify-content: center;
}

section.btn-auto-con button {
    width: 100%;
    background-color: transparent;
    color: chocolate;
}

section.history-container {
    padding: 1rem;
    margin-top: 2rem;
    background-color: #f9f9f9;
    border-radius: 4px;
}

section.history-container div:first-child {
    margin-bottom: 1rem;
}

section.history-container div h3 {
    color: #5f6a75;
    font-weight: 400;
    margin-right: 1rem;
}

section.history-container div span {
    font-size: .86em;
    color: #5f6a75;
    cursor: pointer;
    transition: .2s;
}

section.history-container div span:hover {
    opacity: .9;
}

.history {
    border-bottom: 1px solid #ddd;
    padding: 0;
    margin: 0;
}

.history p {
    margin: 0;
    padding: .6rem 0;
    color: #002;
    font-size: .9em;
}

.history p:first-child {
    color: #708090;
    margin-right: 1rem;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.3);
    justify-content: center;
    z-index: 9999;
    display: none;
}

.modal-content {
    min-width: 14rem;
    border-radius: 10px;
    background-color: #fff;
    padding: .6rem 1.2rem;
    margin: 1rem;
    animation: scale-in .2s;
    -webkit-animation: scale-in .2s;
}

@keyframes scale-in {
    from {transform: scale(.7);}
    to {transform: scale(1);}
}

@-webkit-keyframes scale-in {
    from {transform: scale(.7);}
    to {transform: scale(1);}
}

.modal-content header svg {
    transition: .2s;
    border-radius: 50%;
    height: auto;
    width: auto;
    cursor: pointer;
    margin-right: -.5rem;
    padding: .5rem;
    font-size: 1.3rem;
}

.modal-content header svg:hover {
    background-color: #eeecec;
}

.modal-title {
    width: 100%;
}

form {
    flex-direction: column;
    margin-top: 1.2rem;
}


form label {
    align-self: flex-start;
    justify-self: flex-start;
    font-size: .9em;
}

form select {
    width: 100%;
    margin: .6rem 0;
    padding: .8rem;
}

form button {
    width: 100%;
    margin-top: 1rem;
}

footer {
    text-align: center;
    margin-top: 3rem;
    font-size: 1em;
    padding: .5rem 1rem;
    color: #657280;
    background-color: rgb(241, 243, 244);
}

footer a {
    color: dodgerblue;
    text-decoration: none;
}

footer a:hover { text-decoration: underline; }

footer small b { color: #002; }


@media screen and (max-width: 490px) {
    button {
        margin: .4rem;
    }
}

@media screen and (max-width: 460px) {
    button {
        margin: .8rem;
    }
}

@media screen and (max-width: 420px) {
    main {
        padding: 2rem;
    }
    section.history-container {
        width: 100%;
    }
}

@media screen and (max-width: 280px) {
    .modal-content {
        height: 100vh;
        width: 100vw;
        border-radius: 0px;
        margin: 0;
    }
}